:root{
    --white:#FFFFFF;
    --black:#000000;
    --very-ligth-grey:#EEEEEE;
    --ligth-grey:#DEDEDE;
    --grey-background:#F4F4F4;
    --grey-fonts:#6B6B6B;
    --purple:#5B17AA;
}
.Header{
    height: 100px ;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center ;
}
.Header a{
    text-decoration: none;
    color: var(--black);
}
.Header h1{
    font-family: 'Bebas Neue', cursive;   
    font-size: 48px;
    font-weight: lighter;
}
.Header .miscellany{

    margin-right: 50px;
    display: flex; 
    justify-content: space-between;
    width: 250px;
    
}
.Header .miscellany .cart{
    width: 32px;
    height: 38px;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
}
.Header .miscellany .user-icon{
    width: 32px;
    height: 38px;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
}
.Header .miscellany .loggin-button{
    background: none;
    border: 2px solid var(--black);
    width: 109px;
    height: 44px;
}
.Search {
    margin-left: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
   
    height: 30px;
}
.Search input{
    font-size:18px;
    font-weight: 100;
    height: 38px;
    width: 600px;
    padding-left: 30px;    
    color:var(--grey-fonts);
    background-color: var(--very-ligth-grey);
    border: none;
}

.Search .search-icon{
    width: 40px;
    height: 38px;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
    background-color: var(--ligth-grey);
    border:10px solid var(--ligth-grey)
}


.Search .search-icon:hover{
    cursor: pointer;
}

.NavCategories{
    width: 100vw;
    height: 80px;
    color: var(--white);
    background-color: var(--black);
    display: flex;    
    justify-content: center;
    align-items: center;
}
.NavCategories ul{
    width: 100vw;
    display: flex;    
    justify-content:center;    
}
.NavCategories ul li{
    margin: 20px;
    font-size: 28px;
    padding-right: 40px;  
    height: 48px;    
    list-style: none;
    border-right:2px solid var(--white) ;
}
.NavCategories ul li:nth-last-child(1){
    border:none;
}

.Carrousel{
    width: 100vw;
}
.Carrousel img{
    
    width: 100vw;
    height: 500px;
    transition: 2s;
    
}
.Carrousel button{
    position: relative;
    width: 62px;
    height: 62px;
    border: none;
    color: var(--white);
    background-color: rgba(0, 0, 0,0.4);
    
}
.Carrousel .next {
    
    bottom:300px;
    left: 92rem;
}
.Carrousel .previous {
    
    bottom:300px;
    left: 10px;
}
.Featured-Categories{
    display: flex;
    flex-direction: row;
    font-family: 'Roboto', sans-serif;;
    justify-content: space-evenly;
    color: var(--white);
    margin-top: -100px;
}
.Featured-Categories img{
    width: 640px;
    height: 440px;

}
.Featured-Categories h3{
    font-size: 62px;
    font-style: italic;
    text-align: center;
    position: relative;
    top: 380px;
    margin: 0 auto;                    
}
footer{
    width: 100vw;
    height: 400px;
    background-color: var(--black);
    color:var(--white);
    display: flex;
    justify-content: space-evenly;
    
    margin-top: 50px;
}

.list-Footer{
    list-style: none;    
    display: flex;
    flex-direction: column;    
    margin-top :80px; ;

}
.list-Footer li{
    margin-top: 10px;
}
.social-media-list{
    display: flex;
    
}

.Card{
    width: 320px;
    height: 500px;
    margin: 20px;
    background-color: var(--grey-background);
 }
 .Card img{
    width: 320px;
    height:350px;

 }
 .Card p{
     
     font-weight:bold;
     text-align: start;
     margin: 10px;
 }
 .Card .price{
     color: var(--purple);
 }
 .Card button{
    display: flex;
    justify-content: center;
    align-items: center;
    width:280px;
    height: 34px;
    border: none;
    background-color: var(--black)    ;
    color: var(--white);
    margin: 20px  auto;
 }

 .Card button img{
     width: 20px;
     height: 20px;
 }
 
.MasBuscados{
    margin-top: 24px;
    text-align: center;
    height: 600px;
    width: 100vw;
   
}
.MasBuscados h2{
    font-family: 'Bebas Neue', cursive;
    font-size: 44px;  
}
.products-carrousel{
    width: 80vw;
    margin: auto;        
    display: flex;
    overflow-x: scroll;
    overscroll-behavior-x: contain;

}


.Products{
    height: 100%;
    padding: 10px;
    width: 100vw;
    display: grid;
    grid-template-columns: repeat(auto-fit, 280px);    
    justify-content:center;
    grid-gap: 50px;
    gap: 50px;
    margin:10px auto;
    
}

